<!--  -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="customer" ref="customerForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="id" class="hidden" prop="id">
                <el-input type="hidden" v-model="customer.id"/>
            </el-form-item>
            <el-form-item label="" prop="name">
                <el-input v-model="customer.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="officeId">
                <sui-office v-model="customer.officeId"  placeholder="" :disabled="true"/>
            </el-form-item>
            <el-form-item label="" prop="cardNo">
                <el-input v-model="customer.cardNo" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="level">
                <el-select v-model="customer.level" placeholder="" :disabled="dialog.disabled" >
                    <el-option v-for="dict in levelDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="phone">
                <el-input v-model="customer.phone" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="assets">
                <el-input v-model="customer.assets" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="label">
                <el-input type="textarea" v-model="customer.label" placeholder="" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel"> </el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('customerForm')"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
export default {
    components: {

    },
    name: "customerForm",
    data() {
      return {
        //
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //
        customer: {
            id: "",
            name: "",
            officeId: this.$store.getters.userInfo.officeId,
            cardNo: "",
            level: "",
            phone: "",
            assets: "",
            label: ""
        },
        //
        rules: {
            name: [{required: true, message: '', trigger: 'blur'}],
            officeId: [{required: true, message: '', trigger: 'change'}],
            cardNo: [{required: true, message: '', trigger: 'blur'}],
            level: [{required: true, message: '', trigger: 'change'}],
            phone: [{required: true, message: '', trigger: 'blur'}],
            assets: [{required: true, message: '', trigger: 'blur'}],
            label: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //
        levelDict() {
            return this.$store.getters.getDictArray('customer_level');
        },
    },
    methods: {
        //
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getCustomer(option.id)
            }
        },
        //id
        getCustomer(id) {
            this.$request.doGet("/api/customer/get?id="+id).then(res => {
                if (res.isOk) {
                    this.customer = res.obj;
                }
            });
        },
        //
        cancel: function () {
            this.dialog.open = false
        },
        //
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//
                    that.$request.doPost('/api/customer/sub', that.customer).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //
        showDialog(title, disabled) {
            this.resetForm(this.$refs.customerForm);//
            this.dialog.open = true;//
            this.dialog.title = title;//
            this.dialog.disabled = disabled;//
        }
    }
}
</script>

<style scoped>
</style>



