<!--  -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="product" ref="productForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="id" class="hidden" prop="id">
                <el-input type="hidden" v-model="product.id"/>
            </el-form-item>
            <el-form-item label="" prop="picture">
                <sui-photo type="product" v-model="product.picture" :disabled="dialog.disabled"/>
            </el-form-item>
            <el-form-item label="" prop="name">
                <el-input v-model="product.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="productType">
                <el-select v-model="product.productType" placeholder="" :disabled="dialog.disabled" >
                    <el-option v-for="dict in productTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="price">
                <el-input v-model="product.price" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="num">
                <el-input v-model="product.num" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="remarks">
                <el-input type="textarea" v-model="product.remarks" placeholder="" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel"> </el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('productForm')"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
import suiPhoto from "@/components/sui-photo/sui-photo";
export default {
    components: {
        suiPhoto,
    },
    name: "productForm",
    data() {
      return {
        //
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //
        product: {
            id: "",
            picture: "",
            name: "",
            productType: "",
            price: "",
            num: "",
            remarks: ""
        },
        //
        rules: {
            picture: [{required: true, message: '', trigger: 'change'}],
            name: [{required: true, message: '', trigger: 'blur'}],
            productType: [{required: true, message: '', trigger: 'change'}],
            price: [{required: true, message: '', trigger: 'blur'}],
            num: [{required: true, message: '', trigger: 'blur'}],
            remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //
        productTypeDict() {
            return this.$store.getters.getDictArray('product_type');
        },
    },
    methods: {
        //
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getProduct(option.id)
            }
        },
        //id
        getProduct(id) {
            this.$request.doGet("/api/product/get?id="+id).then(res => {
                if (res.isOk) {
                    this.product = res.obj;
                }
            });
        },
        //
        cancel: function () {
            this.dialog.open = false
        },
        //
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//
                    that.$request.doPost('/api/product/sub', that.product).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //
        showDialog(title, disabled) {
            this.resetForm(this.$refs.productForm);//
            this.dialog.open = true;//
            this.dialog.title = title;//
            this.dialog.disabled = disabled;//
        }
    }
}
</script>

<style scoped>
</style>



