<!--  -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="activity" ref="activityForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="id" class="hidden" prop="id">
                <el-input type="hidden" v-model="activity.id"/>
            </el-form-item>
            <el-form-item label="" prop="title">
                <el-input v-model="activity.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="beginDate">
                <el-date-picker v-model="activity.beginDate" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
            </el-form-item>
            <el-form-item label="" prop="endDate">
                <el-date-picker v-model="activity.endDate" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
            </el-form-item>
            <el-form-item label="" prop="content">
                <el-input type="textarea" v-model="activity.content" placeholder="" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel"> </el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('activityForm')"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
export default {
    components: {

    },
    name: "activityForm",
    data() {
      return {
        //
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //
        activity: {
            id: "",
            title: "",
            beginDate: "",
            endDate: "",
            activityStatus: "",
            content: ""
        },
        //
        rules: {
            title: [{required: true, message: '', trigger: 'blur'}],
            beginDate: [{required: true, message: '', trigger: 'change'}],
            endDate: [{required: true, message: '', trigger: 'change'}],
            activityStatus: [{required: true, message: '', trigger: 'change'}],
            content: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //
        activityStatusDict() {
            return this.$store.getters.getDictArray('activity_status');
        },
    },
    methods: {
        //
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getActivity(option.id)
            }
        },
        //id
        getActivity(id) {
            this.$request.doGet("/api/activity/get?id="+id).then(res => {
                if (res.isOk) {
                    this.activity = res.obj;
                }
            });
        },
        //
        cancel: function () {
            this.dialog.open = false
        },
        //
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//
                    that.$request.doPost('/api/activity/sub', that.activity).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //
        showDialog(title, disabled) {
            this.resetForm(this.$refs.activityForm);//
            this.dialog.open = true;//
            this.dialog.title = title;//
            this.dialog.disabled = disabled;//
        }
    }
}
</script>

<style scoped>
</style>



