<!--  -->
<template>
    <div class="app-container">
        <!--  -->
        <activity-form ref="activityDialog" @reloadList="getList"/>
        <!--  -->
        <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
            <el-form-item label="" prop="title">
                <el-input v-model="queryForm.title" placeholder="" size="small"/>
            </el-form-item>
            <el-form-item label="" prop="beginDate">
                <el-date-picker v-model="queryForm.beginDate" placeholder="" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
            </el-form-item>
            <el-form-item label="" prop="endDate">
                <el-date-picker v-model="queryForm.endDate" placeholder="" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
            </el-form-item>
            <el-form-item label="" prop="activityStatus">
                <el-select v-model="queryForm.activityStatus" placeholder="" size="small" clearable>
                    <el-option v-for="dict in activityStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
            </el-form-item>
        </el-form>
        <!--  -->
        <el-row :gutter="10" class="mb8">
            <el-col :span="1.5">
                <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['activity:add']"></el-button>
            </el-col>
            <el-col :span="1.5">
                <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['activity:delete']"></el-button>
            </el-col>
            <right-toolbar @queryTable="getList"></right-toolbar>
        </el-row>
        <!--  -->
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
            <el-table-column type="selection" align="center" width="50"/>
            <el-table-column label="id"  prop="id" v-if="false"/>
            <el-table-column label="" type="index" width="50"/>
            <el-table-column label="" prop="title" sortable/>
            <el-table-column label="" prop="activityStatus" sortable>
                <template slot-scope="scope">
                    <span v-for="dict in activityStatusDict" v-if="dict.dictValue==scope.row.activityStatus">{{dict.dictLabel}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="content" show-overflow-tooltip sortable/>
            <el-table-column label="" align="center"  width="380" fixed="right">
                <template slot-scope="scope">
                    <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['activity:view']"></el-button>
                    <el-button size="mini" type="primary" :disabled="scope.row.activityStatus==2" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['activity:edit']"></el-button>
                    <el-button size="mini" type="primary" :disabled="scope.row.activityStatus==2" plain icon="el-icon-edit" @click="subActivity(scope.row)" v-hasPermi="['activity:edit']"></el-button>
                    <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['activity:delete']"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :background="true"
                :current-page="queryForm.current"
                :page-sizes="[5,10,20,30,50]"
                :page-size="queryForm.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import activityForm from './activityForm'
    export default {
        components: {activityForm},
        data() {
            return {
              loading: true,//
              selectedData: [],//
              total: 0, //
              dataList: [],
              queryForm: {
                current: 1,
                size: 10,
                id: "",
                title: "",
                beginDate: "",
                endDate: "",
                activityStatus: "",
                content: ""
            }
          };
        },
        mounted() {
            //
            this.getList();
        },
        computed: {
            //
            activityStatusDict() {
                return this.$store.getters.getDictArray('activity_status');
            },
        },
        methods: {
          subActivity(row){
            const that = this;
            that.showConfirm("?", function () {
               row.activityStatus = 2;
               that.$request.doPost('/api/activity/sub', row).then(res => {
                  if (res.isOk) {
                    that.showSuccess("");
                    that.getList();
                 };
              });
            })
          },
            //
            handleQuery() {
                this.queryForm.current = 1;
                this.getList();
            },
            //
            getList() {
                this.$request.doPost('/api/activity/getPage', this.queryForm).then(res => {
                    if (res.isOk) {
                        this.loading = false;
                        this.dataList = res.obj.records;
                        this.total = res.obj.total;
                    }
                })
            },
            //
            resetQuery() {
                this.$refs['queryForm'].resetFields();
            },
            //
            openDialog(option) {
                this.$refs.activityDialog.open(option);
            },
            //
            handleSizeChange: function (size) {
                this.queryForm.size = size;
                this.getList();
            },
            //
            handleCurrentChange: function (currentPage) {
                this.queryForm.current = currentPage;
                this.getList();
            },
            //
            handleSelectionChange(data) {
                this.selectedData = data;
            },
            //
            handleDeleteAll() {
                const that = this;
                that.showConfirm('?', function () {
                    if (that.selectedData.length > 0) {
                        that.selectedData.forEach(function (item, index) {
                            that.delete(item.id);
                        });
                        that.showSuccess("")
                    } else {
                        that.showWarn("")
                    }
                });
            },
            //
            handleDelete(id) {
                const that = this;
                that.showConfirm('?', function () {
                    that.delete(id);
                    that.showSuccess("");
                });
            },
            //
            delete(id) {
                this.$request.doGet('/api/activity/delete?id=' + id).then(res => {
                    if (res.isOk) {
                        this.getList();
                    };
                });
            },
        }
    };
</script>



